import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let Box = styled.div`
    paddding: 20px;
`;

let Title2 = styled.h4`
    font-size: 25px;
    color: ${ props => props.color2 }
`;

function Detail(props) {
    let [alert, setAlert] = useState(true);
    let [inputData, setInputDate] = useState('');

    useEffect(()=>{
        let timer = setTimeout(()=>{setAlert(false)}, 2000);

        return ()=>{clearTimeout(timer)}
    }, [alert]);

    useEffect(()=>{
        return function() {
            console.log(inputData);
        }
    }, []);

    let history = useHistory();
    let { id } = useParams();

    return (
    <div className="container">
        <Box>
            <Title2 className='red'>Detail</Title2>
        </Box>

        <input onChange={(e)=>{ setInputDate(e.target.value) }}/>

        {
            (alert) ? 
            <div className="my-alert">
                <p>재고가 얼마 남지 않았습니다</p>
            </div> : null
        }
        
        <div className="row">
        <div className="col-md-6">
            <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
        </div>
        <div className="col-md-6 mt-4">
            <h4 className="pt-5">{props.shoes[id].title}</h4>
            <p>{props.shoes[id].content}</p>
            <p>{props.shoes[id].price}원</p>
            <Stock stock={props.stocks[id]}></Stock>
            <button className="btn btn-danger" onClick={()=>{ props.setStocks([9, 10, 11]) }}>주문하기</button> 
            <button className="btn btn-danger" onClick={()=>{history.goBack()}}>뒤로가기</button> 
        </div>
        </div>
    </div>
    )
}

function Stock(props) {
    return (
        <p>Stock : {props.stock}</p>
    )
}

export default Detail;